label{
  font-family: 'Avenir Next LT W01 Demi', sans-serif;
  font-weight: normal;
  display: block;
  letter-spacing: 1px;
  color: #094C3D;
}

input{
  font-family: 'AvenirNextLTW01-Medium', sans-serif;
  width: 100%;
  height: 40px;
  border: 2px solid #094C3D;
  padding: 10px;
  transition: 0.3s all;
  background-color: #f9fbf9;
  margin-bottom: 30px;
}

textarea{
  border: 2px solid #094C3D;
  padding: 10px;
  transition: 0.3s all;
  margin-bottom: 30px;
  background-color: #f9fbf9;
}


input:focus, textarea:focus{
  outline: none !important;
  background-color: #fff4c1;
}

























